.register-container {
    background-color: rgba(0, 0, 0, 0.21);
    position: fixed;
    z-index: 10;
    width: 100%;
    height: 100%;
    .form-register {
      background-color: rgb(248, 248, 248);
      width: 45%;
      padding: 40px;
    //   border-radius: 10px;
      margin: 100px auto;
      animation: openModal ease-out 500ms;
      .header{
          display: flex;
          justify-content: space-between;
      }
      .form {
        display: block;
        width: 100%;
        &Field {
          width: 100% !important;
          background-color: rgb(243, 243, 243);
          margin: 10px 0;
          border: 1px solid rgb(235, 235, 235);
          padding: 5px;
          height: 40px;
        }
        &Error {
          color: red;
        }
        .btn_submit {
          width: 100%;
          margin: 10px 0;
          background-color: rgb(87, 87, 87);
        }
      }
      
    }
  }
  
  @keyframes openModal {
      from {
        transform: translateY(-100px);
        opacity: 0;
      }
      to {
        transform: translateY(0);
        opacity: 1;
      }
    }